import React, { Component } from 'react'
import styled from 'styled-components'
import axios from 'axios'
import papaparse from 'papaparse'

export const ScMainContainer = styled.div`
  margin: 0;
  padding: 0 50px 50px 50px;
  font-family: sans-serif;
`

export const ScTextarea = styled.textarea`
  width: 100%;
`

export const ScTextSucceeded = styled.span`
  font-weight: bold;
  color: green
`

export const ScTextFailed = styled.span`
  font-weight: bold;
  color: red
`

export const ScRssItemTemplate = styled.div`
  padding: 0 0 0 50px;
`

export const ScCsvErrors = styled.div`
  padding: 0 0 0 50px;
`

export const ScRequestStatus = styled.div`
  padding: 0 0 0 50px;
`

export default class Main extends Component {
  constructor () {
    super()

    const rssItemTemplate = [
      '<item rdf:about="{{resource id}}">',
      '  <cl:category>{{category}}</cl:category>',
      '  <cl:area>{{area}}</cl:area>',
      '  <cl:subarea>{{subarea}}</cl:subarea>',
      '  <cl:neighborhood>{{neighborhood}}</cl:neighborhood>',
      '  <cl:mapLocation city="{{city}}"',
      '                  state="{{state}}"',
      '                  postal="{{postal code}}" />',
      '  <cl:replyEmail privacy="A">pros@pared.com</cl:replyEmail>',
      '  <cl:job_basics company_name="Pared"',
      '                employment_type="part-time"',
      '                is_nonprofit="0" />',
      '  <title>{{title}}</title>',
      '  <description><![CDATA[{{description}}]]></description>',
      '</item>'
    ].join('\n')

    this.state = {
      rssItemTemplate,
      csvFileName: null,
      csvErrors: [],
      itemCount: 0,
      rss: '',
      requestLoading: false,
      requestType: null,
      requestResponse: null,
      requestSucceeded: false,
      showResponseDetails: true,
      showRssItemTemplateDetails: false
    }

    this.onFileChange = this.onFileChange.bind(this)
    this.onValidateRss = this.onValidateRss.bind(this)
    this.onPost = this.onPost.bind(this)
    this.onRssChange = this.onRssChange.bind(this)
    this.onRssItemTemplateChange = this.onRssItemTemplateChange.bind(this)
    this.onToggleResponDetails = this.onToggleResponDetails.bind(this)
    this.onToggleRssItemTemplateDetails = this.onToggleRssItemTemplateDetails.bind(this)
  }

  onFileChange (event) {
    this.setState({
      csvErrors: [],
      csvFileName: null
    })

    const files = event.target.files
    if (files && files[0]) {
      const filename = files[0].name

      papaparse.parse(files[0], {
        header: true,
        complete: (results) => {
          const csvHeaders = results.meta.fields
          const csvData = results.data

          if (csvData.length === 0) {
            throw (Error('No data in the CSV file.'))
          }

          if (!csvHeaders && csvHeaders.length === csvData[0].length) {
            throw (Error('The header might be corrupted in the CSV file.'))
          }

          if (results.errors && results.errors.length > 0) {
            this.setState({
              csvErrors: results.errors
            })
          }

          this.generateRss(csvData)

          this.setState({
            csvFileName: filename
          })
        },
        error: (errors) => {
          throw (Error(errors))
        }
      })
    }
  }

  generateRss (csvData) {
    const rssPrefix = [
      '<?xml version="1.0"?>',
      '<rdf:RDF xmlns="http://purl.org/rss/1.0/" xmlns:rdf="http://www.w3.org/1999/02/22-rdf-syntax-ns#" xmlns:cl="http://www.craigslist.org/about/cl-bulk-ns/1.0">'
    ].join('\n')

    const rssSuffix = ['</rdf:RDF>'].join('\n')

    let rssChannel = [
      '<channel>',
      '<items>'
    ]

    csvData.forEach(data => {
      rssChannel.push(`<rdf:li rdf:resource="${data['resource id']}"/>`)
    })

    rssChannel = [
      ...rssChannel,
      '</items>',
      '<cl:auth username="listuser@bogus.com" password="p0stp@rty" accountID="14" />',
      '</channel>'
    ].join('\n')

    let rssItems = []

    csvData.forEach(data => {
      let item = this.state.rssItemTemplate
      Object.keys(data).forEach(key => {
        item = item.replace(new RegExp(`{{${key}}}`, 'g'), data[key])
      })
      rssItems.push(item)
    })

    rssItems = rssItems.join('\n')

    this.setState({
      itemCount: csvData.length,
      rss: [rssPrefix, rssChannel, rssItems, rssSuffix].join('\n'),
      requestType: null
    })
  }

  onValidateRss () {
    this.setState({
      requestLoading: true,
      requestType: 'Validation',
      showResponseDetails: true
    })

    axios.post('https://post.craigslist.org/bulk-rss/validate', this.state.rss, {
      headers: {
        'content-type': 'application/x-www-form-urlencoded'
      }
    })
      .then((response) => {
        this.setState({
          requestLoading: false,
          requestResponse: response,
          requestSucceeded: true
        })
      }).catch((error) => {
        // console.log(error)
        // console.log(Object.keys(error))
        // console.log(error.request)
        // console.log(error.response)
        // console.log(error.config)
        this.setState({
          requestLoading: false,
          requestResponse: error,
          requestSucceeded: false
        })
      })
  }

  onPost () {
    if (window.confirm(`Are you sure to post all ${this.state.itemCount} postings to Craigslist?`)) {
      this.setState({
        requestLoading: true,
        requestType: 'Posting',
        showResponseDetails: true
      })

      axios.post('https://post.craigslist.org/bulk-rss/post', this.state.rss, {
        headers: {
          'content-type': 'application/x-www-form-urlencoded'
        }
      })
        .then((response) => {
          this.setState({
            requestLoading: false,
            requestResponse: response,
            requestSucceeded: true
          })
        }).catch((error) => {
          this.setState({
            requestLoading: false,
            requestResponse: error,
            requestSucceeded: false
          })
        })
    }
  }

  onRssChange (event) {
    this.setState({
      rss: event.target.value,
      requestType: null
    })
  }

  onRssItemTemplateChange (event) {
    this.setState({
      rssItemTemplate: event.target.value
    })
  }

  onToggleResponDetails (event) {
    event.preventDefault()
    this.setState({
      showResponseDetails: !this.state.showResponseDetails
    })
  }

  onToggleRssItemTemplateDetails (event) {
    event.preventDefault()
    this.setState({
      showRssItemTemplateDetails: !this.state.showRssItemTemplateDetails
    })
  }

  render () {
    let csvErrors = null
    if (this.state.csvErrors.length > 0) {
      csvErrors = (
        <ScCsvErrors>
          <br />
          <div><ScTextFailed>Corrupted rows in the CSV file:</ScTextFailed></div>
          <div>
            <ul>
              {
                this.state.csvErrors.map((error) => {
                  return (
                    <li key={error.row}>
                      <strong>Row {error.row + 1}</strong>: {error.message}
                    </li>
                  )
                })
              }
            </ul>
          </div>
          <div>Note: corrupted rows won't be included in the RSS content generation.</div>
        </ScCsvErrors>
      )
    }

    let requestStatus = null
    if (this.state.requestLoading) {
      requestStatus = <div>Loading ...</div>
    } else {
      if (this.state.requestType) {
        let statusText = null
        if (this.requestSucceeded) {
          statusText = <ScTextSucceeded>{this.state.requestType} succeeded!</ScTextSucceeded>
        } else {
          statusText = <ScTextFailed>{this.state.requestType} Failed ...</ScTextFailed>
        }

        requestStatus = (
          <div>
            { this.state.showResponseDetails
              ? (
                <div>
                  <div>{statusText}</div>
                  <div><ScTextarea rows={6} defaultValue={this.state.requestResponse} /></div>
                </div>
              )
              : null
            }

            <div>
              <a href='/void' onClick={this.onToggleResponDetails}>
                <i>
                  { this.state.showResponseDetails ? 'Hide' : 'Show' } response details
                </i>
              </a>
            </div>
          </div>
        )
      } else {
        requestStatus = null
      }
    }

    return (
      <ScMainContainer>
        <h2>Pared Craigslist Automation</h2>
        <br />

        <h3>RSS item template</h3>
        <ScRssItemTemplate>
          <div>
            <a href='/void' onClick={this.onToggleRssItemTemplateDetails}>
              <i>
                { this.state.showRssItemTemplateDetails ? 'Hide' : 'Show' } details
              </i>
            </a>
          </div>
          { this.state.showRssItemTemplateDetails
            ? (
              <div>
                <br />
                <ScTextarea rows={16} value={this.state.rssItemTemplate} onChange={this.onRssItemTemplateChange} />
              </div>
            )
            : null
          }
        </ScRssItemTemplate>
        <br />
        <br />

        <h3>Please upload the CSV file</h3>
        <div><input type='file' onChange={this.onFileChange} value='' /></div>
        { this.state.csvFileName
          ? <div><br /><i>The file, <strong>{this.state.csvFileName}</strong>, has been processed ~</i></div>
          : null
        }
        {csvErrors}
        <br />
        <br />

        <h3>RSS:</h3>
        <div>
          <button onClick={this.onValidateRss}>Validate RSS</button>
          &#160; &#160; &#160;
          <button onClick={this.onPost}>Post to Craigslist</button>
        </div>
        <br />

        <ScRequestStatus>{requestStatus}</ScRequestStatus>
        <br />

        <div><ScTextarea rows={30} value={this.state.rss} onChange={this.onRssChange} /></div>
        <br />
        <br />
        <br />
      </ScMainContainer>
    )
  }
}



